import React, { Component } from 'react';
import { Col, Row, Image, Button } from 'react-bootstrap';
import './Education.css';

class Education extends Component {
    constructor(props) {
        super(props);
        this.state = {
            edu: true,
            eduName: '',
            eduText: '        .  ,             , ,   Data Science.',
            img: `Za_Nas/edukacija1.jpg`,
            compName: '',
            compText: '       ,          . \n    ,            , , ,   Team building. \n  Brainster Space            ,        .',

        }
    }

    toggleEdu() {
        console.log('edu')
        this.setState({
            edu: true,
            img: 'Za_Nas/edukacija1.jpg'
        })
    }
    toggleComp() {
        console.log('const')
        this.setState({
            edu: false,
            img: '7707.jpg'
        })
    }

    render() {
        return (
            <div className="education">
                <Col lg={11} sm={12} className="headCol">
                    <Row>
                        <Col lg={5} md={12} className="eduCompPart">
                            <div className="titlePart">
                                <h1>{this.state.edu ? `${this.state.eduName}` : `${this.state.compName}`}</h1>
                                <h5>{this.state.edu ? `${this.state.eduText}` : `${this.state.compText}`}</h5>
                            </div>
                            <div className="btnEduComp">
                                <Button className={this.state.edu ? ' ' : 'nameColor '}
                                variant={this.state.edu ? 'dark' : 'warning'} 
                                size="lg" 
                                onClick={() => { this.toggleEdu() }}></Button>
                                <Button className={this.state.edu ? 'nameColor ' : '  '}
                                variant={this.state.edu ? 'warning' : 'dark'} 
                                size="lg" 
                                onClick={() => { this.toggleComp() }}> </Button>
                            </div>
                        </Col>
                        <Col lg={7} md={12}>
                            <div><Image src={require(`../../../Images/${this.state.img}`)} fluid /></div>
                        </Col>
                    </Row>
                </Col>
            </div>
        );
    }
}

export default Education;