import React from 'react';
import { Col, Row, Image } from 'react-bootstrap';
import './OurSpace.css'

function OurSpace() {

    let space = [
        { img: "2", title: "Brainster" },
        { img: "C31", title: " " },
        { img: "C32", title: "  " },
        { img: "C3EXIBITION_1", title: " " },
        { img: "C21", title: "" },
        { img: "C11", title: "" },
        { img: "HOLKONSEDENJE", title: "" },
        { img: "C14", title: " " },
        { img: "KITCHEN_03", title: "Space Kitchen" }
    ]

    return (
        <div className="ourSpace">
            <Col lg={{ span: 11 }} className="firstSpace">
                <Row>
                    <Col sm={12} md={12} lg={3} className="space">
                        <div className="textTitleSpace"><h1> </h1></div>
                        <p> .    150          50 .
                               25  40 .    .
                            <br /><br />
                              .     networking- Brainster Kitchen.
                            <br /><br />
                                 ?
                        </p>
                    </Col>
                    <Col sm={12} lg={9}>
                        <Row>
                            {space.map((el, i) => {
                                return (
                                    <Col sm={6} md={6} lg={4} key={i} className="bild">
                                        <div className="imgDef"><Image src={require(`../../../Images/Renderi/${el.img}.jpg`)} fluid /></div>
                                        <p>{el.title}</p>
                                    </Col>
                                )
                            })}

                        </Row>
                    </Col>
                </Row>
            </Col>
        </div>
    )
}
export default OurSpace;