import React, {useState} from 'react';
import { Row, Col, Card, Button, Modal, Form } from 'react-bootstrap';
import './ZaNas.css';
import { Link } from 'react-scroll';

function ZaNas() {
    const [show, setShow] = useState(false);
    const handleShow = () => setShow(true);
    const handleClose = () => setShow(false);
    const [keyCount, setKeyCount] = useState(300);
    const [validated, setValidated] = useState(false);

    const handleSubmit = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
          event.preventDefault();
          event.stopPropagation();
        }
    
        setValidated(true);
      };
    

    return (
        <div className="zaNas">
                <Col lg={11} sm={12} className="headCol">
                    <h2> </h2>
                    <Row>
                        <Col lg={4} sm={6} xs={12}>
                            <a href="https://brainster.co" target="_blank" rel="noopener noreferrer">
                            <Card >
                            <div className="hover03 column">
                                <div>
                                    <figure>
                                        <Card.Img variant="top" src={require(`../../../Images/Za_Nas/edukacija1.jpg`)} />
                                    </figure>
                                </div>
                            </div>
                                <Card.Body>
                                    <Card.Title></Card.Title>
                                    <Card.Text>
                                              .           sKill
                                        set          .  ,      
                                            .
                                    </Card.Text>
                                    <Button variant="outline-dark"><i className="fas fa-arrow-right"></i></Button>
                                </Card.Body>
                            </Card>
                            </a>
                        </Col>
                        
                        <Col lg={4} sm={6} xs={12}>
                            <a href="/Nastani" rel="noopener noreferrer">
                            <Card >
                                <div className="hover03 column">
                                    <div>
                                        <figure>
                                            <Card.Img variant="top" src={require(`../../../Images/Za_Nas/nastani.jpg`)} />
                                        </figure>
                                    </div>
                                </div>
                                <Card.Body>
                                    <Card.Title></Card.Title>
                                    <Card.Text>
                                                    .
                                            tech  ,       . 
                                        
                                        </Card.Text>
                                        <Button variant="outline-dark"><i className="fas fa-arrow-right"></i></Button>
                                </Card.Body>
                            </Card>
                            </a>
                        </Col>
                        <Col lg={4} sm={6} xs={12}>
                            <a href="#Coworking">
                            <Card >
                                <div className="hover03 column">
                                    <div>
                                        <figure>
                                            <Card.Img variant="top" src={require(`../../../Images/Za_Nas/coworking.jpg`)} />
                                        </figure>
                                    </div>
                                </div>
                                <Card.Body>
                                    <Card.Title>Coworking</Card.Title>
                                    <Card.Text>
                                            tech   ,   .     shared office.
                                                           .
                                        </Card.Text>
                                        <Button variant="outline-dark"><i className="fas fa-arrow-right"></i></Button>
                                </Card.Body>
                            </Card>
                            </a>
                        </Col>
                  
                        <Col lg={4} sm={6} xs={12}>
                            <a href="/EventSpace">
                            <Card >
                                <div className="hover03 column">
                                    <div>
                                        <figure>
                                            <Card.Img variant="top" src={require(`../../../Images/Za_Nas/prostor_za_nastani.jpg`)} />
                                        </figure>
                                    </div>
                                </div>
                                <Card.Body>
                                    <Card.Title>  </Card.Title>
                                    <Card.Text>
                                                tech ?     .   
                                              .
                                        </Card.Text>
                                        <Button variant="outline-dark"><i className="fas fa-arrow-right"></i></Button>
                                </Card.Body>
                            </Card>
                            </a>
                        </Col>
                        <Col lg={4} sm={6} xs={12}>
                        <Link
                            activeClass="active"
                            to="partner"
                            spy={true}
                            smooth={true}
                            offset={-70}
                            duration={1000}
                        >
                            <Card >
                                <div className="hover03 column">
                                    <div>
                                        <figure>
                                            <Card.Img variant="top" src={require(`../../../Images/partnerstva_so_tech_komp.jpg`)} />
                                        </figure>
                                    </div>
                                </div>
                                <Card.Body>
                                    <Card.Title>  Tech </Card.Title>
                                    <Card.Text>
                                               tech     ,        .
                                               tech   .
                                        </Card.Text>
                                        <Button variant="outline-dark"><i className="fas fa-arrow-right"></i></Button>
                                </Card.Body>
                            </Card>
                            </Link>
                        </Col>
                        
                        <Col lg={4} sm={6} xs={12} onClick={handleShow}>
                            <a href="#Inovations">
                            <Card >
                                <div className="hover03 column">
                                    <div>
                                        <figure>
                                            <Card.Img variant="top" src={require(`../../../Images/IMG_7397.jpg`)} />
                                        </figure>
                                    </div>
                                </div>
                                <Card.Body>
                                    <Card.Title>  </Card.Title>
                                    <Card.Text>
                                                         . 
                                               . 
                                        </Card.Text>
                                    <Button variant="outline-dark"><i className="fas fa-arrow-right"></i></Button>
                                </Card.Body>
                            </Card>
                            </a>
                        </Col>
                    </Row>

                </Col>
                <Modal
          show={show}
          onHide={handleClose}
          
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header closeButton>
            <h2> </h2>
          </Modal.Header>
          <Modal.Body>
            <Form noValidate validated={validated} onSubmit={handleSubmit}>
              <Form.Row>
                <Form.Group
                  as={Col}
                  md={12}
                  xs={12}
                  controlId="validationCustom01"
                >
                  <Form.Label className="label">
                       ()
                  </Form.Label>
                  <Form.Control
                    required
                    type="text"
                    placeholder="   "
                  />
                  <Form.Control.Feedback type="invalid">
                       !
                  </Form.Control.Feedback>
                </Form.Group>
              </Form.Row>

              <Form.Row>
                <Form.Group
                  as={Col}
                  md={12}
                  xs={12}
                  controlId="validationCustom02"
                >
                  <Form.Label className="label">
                       ()
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="    "
                  />
                </Form.Group>
              </Form.Row>
              <Form.Row>
                <Form.Group
                  as={Col}
                  md={12}
                  xs={12}
                  controlId="validationCustomTelephone"
                >
                  <Form.Label className="label">
                      ()
                  </Form.Label>
                  <Form.Control
                    required
                    type="email"
                    placeholder="  "
                    aria-describedby="inputGroupPrepend"
                  />
                  <Form.Control.Feedback type="invalid">
                      
                  </Form.Control.Feedback>
                </Form.Group>
              </Form.Row>
              <Form.Row>
                <Form.Group
                  as={Col}
                  md={12}
                  xs={12}
                  controlId="validationCustomTelephone"
                >
                  <Form.Label className="label">
                      ()
                  </Form.Label>
                  <Form.Control
                    required
                    type="number"
                    placeholder="  "
                    aria-describedby="inputGroupPrepend"
                  />
                  <Form.Control.Feedback type="invalid">
                      
                  </Form.Control.Feedback>
                </Form.Group>
              </Form.Row>
              <Form.Row>
                <Form.Group
                  as={Col}
                  md={12}
                  xs={12}
                  controlId="exampleForm.ControlTextarea1"
                >
                  <Form.Label className="label">
                       ()
                  </Form.Label>
                  <Form.Control
                    onKeyUp={(e) =>
                      setKeyCount(300 - Number(e.currentTarget.value.length))
                    }
                    as="textarea"
                    rows="5"
                    maxLength="300"
                    placeholder=" 300 ,     "
                  />
                  <Form.Control.Feedback id="count" type="valid">
                    {keyCount}
                  </Form.Control.Feedback>
                </Form.Group>
              </Form.Row>
              <Modal.Footer>
                <Button variant="light" onClick={handleClose}>
                  
                </Button>
                <Button type="submit" className="btnSubm" variant="dark">
                  <i className="fas fa-arrow-right "></i>  
                </Button>
              </Modal.Footer>
            </Form>
          </Modal.Body>
        </Modal>
        </div>
    )
}
export default ZaNas;